:root {
  --taskbar-1: map-get($ taskbar-colors, 1);
  --taskbar-2: map-get($ taskbar-colors, 2);
  --taskbar-3: map-get($ taskbar-colors, 3);
  --taskbar-4: map-get($ taskbar-colors, 4);
  --taskbar-5: map-get($ taskbar-colors, 5);
}

body {
  background: url("wave.55589e64.svg") center / cover no-repeat fixed;
  margin: 0;
  font-family: Tahoma, Segoe UI, Geneva, Verdana, sans-serif;
}

body #taskbar {
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  background: linear-gradient(#245edc 0%, #3f8cf3 9%, #245edc 18% 92%, #1941a5 100%) center / cover no-repeat;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 8px #0009;
}

body #taskbar #start-button {
  cursor: pointer;
  background: linear-gradient(#407839 0%, #8dbb8c 9%, #369239 18%, #3daa3b 92%, #2e7044 100%) center / cover no-repeat;
  border-radius: 0 10px 10px 0;
  outline: 1px solid #ffffff1a;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 5px 25px 5px 15px;
  display: flex;
  box-shadow: inset 0 0 3px #0000004d;
}

body #taskbar #start-button img {
  filter: drop-shadow(1px 1px 1px #222);
  height: 1.2em;
}

body #taskbar #start-button span {
  text-shadow: 0 5px 12px #000000b3;
  font-size: 1.06em;
  font-style: italic;
  font-weight: bold;
}

body #taskbar #start-button:hover {
  background: linear-gradient(#388b54 0%, #8dbb8c 9%, #4ed753 18%, #3daa3b 92%, #388b54 100%) center / cover no-repeat;
}

body #taskbar #start-button:hover img {
  animation: 1s ease-in-out infinite bounce;
}

@keyframes bounce {
  from {
    translate: 0;
  }

  25% {
    translate: 0 .1em;
  }

  50% {
    translate: 0 -.1em;
  }

  75% {
    translate: 0 .1em;
  }

  100% {
    translate: 0;
  }
}

body #taskbar #start-button:active {
  background: linear-gradient(#1c4529 0%, #6b8f6b 9%, #2a762d 18%, #236222 92%, #1c4529 100%) center / cover no-repeat;
}

body #taskbar #taskbar-status {
  letter-spacing: -.04em;
  background: linear-gradient(#1a95e6 0%, #25b0fe 9%, #1a95e6 18%, #0fa9eb 92%, #015ab6 100%) center / cover no-repeat;
  outline: 1px solid #ffffff1a;
  justify-content: center;
  align-items: center;
  gap: 10px;
  min-height: 100%;
  padding: 5px 25px 5px 15px;
  font-family: Courier New, Courier, monospace;
  font-size: .83em;
  font-weight: 500;
  display: flex;
  box-shadow: inset 0 0 4px #0000007d;
}

body #taskbar #taskbar-status #taskbar-status-icons {
  margin-right: 5px;
  display: flex;
}

body #taskbar #taskbar-status #taskbar-status-icons a {
  display: flex;
}

body #taskbar #taskbar-status #taskbar-status-icons a img {
  object-fit: contain;
  max-height: 1.2em;
}
